import * as React from "react"
import { useState, useEffect } from "react"
import MyImageLight from '../assets/images/icon.png'
import MyImageDark from '../assets/images/icondark.png'
import { setTheme } from '../utils/themes';
import { isBrowser } from '../utils/isbrowser';


const myImageWrapperStyle = {
    display: 'grid',
}

const myImageStyle = {
    width: '96px',
    height: '96px',
    alignSelf: 'center',
}


const PersoImage = () => {
    const [togClass, setTogClass] = useState('dark');
    
    //if(typeof window !== 'undefined' && window.LocalStorage) {
    let theme = localStorage.getItem('theme');

    useEffect(() => {
        if (localStorage.getItem('theme') === 'theme-dark') {
            setTogClass('dark')
        } else if (localStorage.getItem('theme') === 'theme-light') {
            setTogClass('light')
        }
    }, [theme])


if(isBrowser) {
    const toggleDarkMode = () => {
    if (localStorage.getItem('theme') === 'theme-dark') {
        setTheme('theme-light');
        setTogClass('light')
    } else {
        setTheme('theme-dark');
        setTogClass('dark')
    }
    }




    return (
        <div className="persoImage" style={myImageWrapperStyle}>
                {
                    togClass === "light" ?
                    <img src={MyImageLight} alt="my Image" style={myImageStyle} onClick={toggleDarkMode} checked />
                    :
                    <img src={MyImageDark} alt="my Image" style={myImageStyle} onClick={toggleDarkMode} />
                }
        </div>
    )
    } else {
        return <h1>ok</h1>
    }
    //} else {
        //return <h1>OK</h1>
    //}
}

export default PersoImage;